.about-body{
    line-height: 0;
    background: #f5f5f5;
    padding-top: 5rem;
}
.bout{
    font-family: serif;
    font-size: 20px;
    text-align: center;
    color: rgb(231, 157, 19);
    margin: 0rem 0 3rem 0;
}

 .about-body h2 {
    font-size: 40px;
    color: rgb(29, 29, 29);
    text-align: center;
    margin: 0 0 3rem 0;
}

.about-cont{
    /* background:rgb(231, 157, 19); */
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 95%;
    margin: auto;
    padding: 2rem 0 5rem 0;
    /* gap: 3rem; */
}

.bout-content {
    line-height: 20px;
    width: 50%;
    padding: 3rem 0 0 0;
}

.image{
    width: 55%;
}

.image img{
    width: 100%;
}

.bout-content p{
    line-height: 20px;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 2rem;
    text-align: justify;
}

.bout-content h3{
    font-size: 30px;
    text-decoration: underline;
    line-height: 5px;
    margin-bottom: 3rem;
}

