.contact-body {
    line-height: 0;
    /* background: #f9f9f9; */
    /* background: blue; */
    background: #f5f5f5;
    padding: 5rem 0;
    overflow: hidden;
    position: relative;
    height: 100vh;
}

.contact-body img:nth-child(1) {
    width: 500px;
    position: absolute;
    top: -100px;
    left: 1000px;
    opacity: .2;
}

.contact-body img:nth-child(2) {
    width: 500px;
    position: absolute;
    top: 300px;
    left: -220px;
    opacity: .2;
}

.tact {
    font-family: serif;
    font-size: 20px;
    text-align: center;
    color: rgb(231, 157, 19);
    margin: 0rem 0 1rem 0;
}

.contact-us {
    padding: 3.5rem 2rem;
    width: 40%;
    background: #f9f9f9;
    box-shadow: 0px 20px 40px rgba(128, 128, 128, 0.3);
    border-radius: 10px;
    /* margin: 5rem auto 5rem auto; */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.contact-us h2 {
    font-size: 40px;
    color: rgb(29, 29, 29);
    text-align: center;
    margin: 0 0 2rem 0;

}

.contact-us p {
    text-align: center;
    margin: 0 0 3rem 0;

}

.form {
    width: 100%;
    margin: auto;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.form input:nth-child(3) {
    height: 200px;
    padding: 0;
}

input {
    border-radius: 8px;
    border: 2px rgb(231, 157, 19) solid;
    padding: 0rem .5rem;
    margin-bottom: 1rem;
    height: 2.5rem;
    transition: 0.2s ease all;
    font-family: 'Poppins', sans-serif;
}


.form textarea {
    border-radius: 8px;
    border: 2px rgb(231, 157, 19) solid;
    padding: 1rem;
    margin-bottom: 1rem;
    height: 8rem;
    font-family: 'Poppins', sans-serif;

}

.form textarea:focus {
    outline: none;
}

input::placeholder {
    font-family: 'Poppins';
    transition: 0.2s ease all;

}

input:focus::placeholder {
    font-family: 'Poppins';
    color: rgb(231, 157, 19);
    position: absolute;
    top: 0;
    transition: 0.2s ease all;

}

input:focus {
    outline: none;
}

.names {
    display: flex;
    width: 100%;
    gap: 1rem;
    margin: auto;
    padding: .1rem 0;
}

.names input {
    width: 50%;
}